

.projectsMarginTop {
    margin-top: 5em;
}
#projects-menu-fixed {
    position: fixed;
    width: 48%;
    max-width: 48%;
    box-sizing: border-box;
    padding-right: 1em;
}

#projects-menu-inner {
    text-align: left;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    
    font-family: PangramDisplay, 'Arial Narrow', sans-serif;
    font-size: 2.7em;

    left: 0;

    display: inline-block;
    overflow-wrap: break-word;
    word-wrap: break-word;
}
#projects-menu-inner p {
    width:fit-content;
    padding: 0;

    margin: 0;
    margin-bottom: .4em;

    /* letter-spacing: .02em; */

    transition: color 400ms ease-in-out, transform 200ms ease-out, letter-spacing 300ms ease-out, opacity 200ms ease-in-out, font-size 400ms 150ms ease-out;;
    transform-origin: 0% 50%;



}
#projects-menu-inner p:hover {
    cursor: pointer;

    transform: skew(-2deg, 0) scale(1.2);
    letter-spacing: .01em;
    color: var(--c-black);
}
.inactiveCategory {
    color: var(--c-gray);
}
.activeCategory {
    opacity: 1;
    color: var(--c-blue);
    transform: skew(-14deg, 0) translateX(1%);
    letter-spacing: 0em;
}
@media (max-width: 800px) {
    #projects-menu-inner {
        font-size: 5.3vw;
    
    }
}
@media (max-height: 400px) {
    /* TODO - turn into stacked divs instead of split for mobile */
    #projects-menu-fixed, #projects-menu-tablecell {
        height: 20em;
        margin-top: var(--navHeight);
        position: absolute;
        /* overflow-y: scroll; */
    }
}

.blueOverlay {
    position: absolute;
    width: 100%;
    aspect-ratio: 1;

    background-color: var(--c-blue);
    border-radius: var(--img-radius);
    z-index: 100;
    opacity: 0;
    transition: 200ms ease-in-out;
}
.bo1 {
    mix-blend-mode:lighten;
    opacity: 0;
}
.bo2 {
    background-color: rgb(139, 189, 255);
    mix-blend-mode:darken;
    opacity: 0;
}
.bo3 {
    /* background-color: rgb(134, 168, 255); */
    background-color: rgb(0, 85, 255);
    mix-blend-mode:color;
}

.thumbContainer img{
    /* filter: brightness(calc(50%)) grayscale(100%) contrast(80%); */
    z-index: 99;
    transition: 400ms 0ms ease-out;
}
.thumbLinkContainer {
    width: 100%;
    position: relative;
}
.thumbLinkContainer img, .thumbLinkContainer h .thumbLinkContainer h2{
    cursor: pointer;
}
.thumbContainer{
    /* .thumbContainer, .thumbContainer img, .thumbContainer  { */
    margin: 0;
    z-index: 100;

    font-size: 1.4em;
    font-family: PangramHeader, 'Arial Narrow', sans-serif;
    /* margin-bottom: 2em; */
    text-align: left;
    width: 100%;
    height: 100%;
    cursor: pointer;

    transition: 300ms ease-out;
}
.thumbContainer:hover .blueOverlay {
    opacity: 1;
}
.thumbContainer:hover img {
    outline: .25em var(--c-blue) solid;
}
.thumbContainer:hover {
    color: var(--c-blue)
}


.thumbContainer h1{
    margin: 0;
    /* font-family: PangramDisplay; */

    font-weight: 600;
    
    letter-spacing: .02em;
    font-size: 1.1em;
    height: .99em;

    margin-right: .3em;
    margin-top: .4em;
    margin-bottom: .1em;

}
.thumbContainer h2{
    margin: 0;
    
    font-family: PangramCompact, Arial, sans-serif;
    font-size: .58em;
    letter-spacing: .02em;
    margin-bottom: .15em;
}
.thumbContainer h1, .thumbContainer h2{
    display: inline-block;
    width: fit-content;
    vertical-align: bottom;
    margin-left: 2px;
}
.thumbContainer img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}

.placeholderContainer {
    width: 100%;
    aspect-ratio: 1;
    position: relative;
    z-index: 100;
}

.placeholderThumb {
    background-color: var(--c-grayLight);
    /* background-color: red; */
    width: 100%;
    height: 100%;
    position: absolute;
}


.thumbContainer img, .placeholderThumb {
    border-radius: var(--img-radius);
    z-index: 100;
}

#projectGrid {
    display: grid;
    transition: 400ms ease-in-out;
    min-height: 90vh;
    grid-template-rows: min-content;
}

.grid1 {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 2.5em;
}
.grid2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 2em 1em;
}
.grid3 {
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: .8em;
}

.grid2 h1{
    font-size: .7em;
    font-size: 2vw;
    margin-right: 0;
    margin-top: .2em;
    vertical-align: top;
}
.grid2 h2{display: none;}

.grid3 h1{display: none;}
.grid3 h2{display: none;}
.grid3 .thumbContainer {
    aspect-ratio: 1;
}

  

#gridBtnContainer {
    margin-top:1em;
    margin-right: 1em;
    width: 100%;

}

#gridBtnContainer button {
    border-radius: 0;
    height: 2.4em;
    aspect-ratio: 1;
    margin: 1em 0;
    margin-right: 1em;

    mask-position: center;
    mask-repeat: no-repeat;
    mask-size: 95%;

    background-color: var(--c-gray);
    opacity: .95;

    transition: 300ms ease-in-out;
}


