#gridBtnContainer button:hover {
    mask-size: 100%;
    background-color: var(--c-black);
}

#gridBtnContainer .gridBtnActive {
    background-color: var(--c-blue);
}

#archiveLink {
    font-size: .4em;
}

.thumbFadeOut {
    animation-name: thumbFade;
    animation-duration: 300ms;
    animation-iteration-count: 1;
    animation-direction: normal;
}
.thumbFadeIn {
    animation-name: thumbFade;
    animation-duration: 300ms;
    animation-iteration-count: 1;
    animation-direction: reverse;
}
@keyframes thumbFade {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

#toCategoriesDiv {
    display: none;
    width: 100%;
    font-family: PangramDisplay ;
    margin-top:80px;
}
#toCategoriesDiv p {
    color: var(--c-black);
    font-size: 9vw;
    line-height: .8em;
}




@media only screen and (max-width: 768px) {

    .projectsMarginTop {
        margin-top: 2em;
    }


    #gridBtnContainer {
        margin:0;
        width: 100%;
        text-align: center;
    }
    #gridBtnContainer button{
        margin: 1.5em 3vw;
        height: 10vw;
    }

    #toCategoriesDiv {
        display: block;
    }

    #projects {
        padding: 0;

    }
    #projects-menu-fixed {
        position: relative;

    }

    #projects-menu-inner {
        font-size: 9vw;
    }
    #projects-menu-inner p {
        margin-bottom: .2em;
        width: 100%;
        text-align: center;
        transform-origin: 50% 50%;
    }


    #projectGrid {

        min-height: 10vh; 
        width:auto;

        padding: 24px;
    }

    .grid2 {
        gap: 28px 24px;
    }
    .grid2 h1{
        font-size: 4.5vw;
    }


    .grid3 {
        grid-template-columns: repeat(3, minmax(0, 1fr));
        gap: 8px;
    }

    .thumbContainer h1 {
        margin-top: .4em;
    }

    .thumbContainer h1 , .thumbContainer h2 {
        letter-spacing: 0;
    }

}

@media (hover: none) {
    
    #gridBtnContainer button:hover {
        mask-size: inherit;
        background-color: var(--c-blue);
    }

    #projects-menu-inner p:hover {
        transform: skew(-14deg, 0) translateX(1%);
        letter-spacing: inherit;
        color: var(--c-blue);
    }
}

/* Tag Button Styles */
.tagButton {
    background: none;
    border: 2px solid var(--c-blue);
    color: var(--c-blue);
    font-family: PangramDisplay, 'Arial Narrow', sans-serif;
    font-size: 0.6em;
    padding: 0.2em 0.4em 0.45em;
    margin: 0 0.4em 0 0;
    border-radius: .5em;
    cursor: pointer;
    transition: all 200ms ease-in-out;
    text-transform: uppercase;
    display: inline-block;
    width: auto;
    text-align: center;
    max-width: calc(50% - 0.6em);
    box-sizing: border-box;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.tagButton:hover {
    transform: scale(1.05);
    background-color: var(--c-blue);
    color: white;
}

.tagButtonActive {
    background-color: var(--c-blue);
    color: white;
}

.tagButtonInactive {
    background: none;
    color: var(--c-blue);
}

.deselectAllButton {
    background: none;
    border: none;
    border-radius: 0;
    color: var(--c-gray);
    font-family: PangramDisplay, 'Arial Narrow', sans-serif;
    font-size: 0.5em;
    padding: 0;
    margin: 0;
    cursor: pointer;
    transition: color 150ms ease-in-out, opacity 150ms ease-in-out;
    text-transform: uppercase;
    display: none;
    width: auto;
    text-align: center;
    line-height: 1;
    overflow: hidden;
}

.deselectAllButton.visible {
    display: inline;
}

.tagsLabel {
    color: var(--c-black);
    font-family: PangramDisplay, 'Arial Narrow', sans-serif;
    font-size: 0.5em;
    text-transform: uppercase;
    margin-right: 1em;
    line-height: 1;
}

.tagsContainer {
    height: min-content;
    margin-bottom: 0.3em;
    line-height: 0;
}

.tagsWrapper {
    display: inline-block;
    width: 100%;
}

.deselectAllButton:hover {
    color: var(--c-blue);
}

@media (max-width: 800px) {
    #projects-menu-fixed {
        width: 100%;
        max-width: 100%;
        padding-right: 0;
        text-align: center;
    }
    
    .tagsWrapper {
        text-align: center;
        margin-bottom: 0.5em;
    }
    
    .tagButton {
        font-size: 5.3vw;
        padding: 0.1em 0.5em 0.35em;
        margin: 0.1em 0.3em 0.1em 0;
        max-width: 90%;
    }
    .tagsContainer {
        margin: 0;
        font-size: 1.2em;
        margin-bottom: 0.2em;
    }
    .deselectAllButton {
        display: block;
        margin: 0 auto;
        margin-top: 0.3em;
        height: 0;
        opacity: 0;
        transition: height 300ms ease-in-out, opacity 300ms ease-in-out, margin-top 300ms ease-in-out;
    }
    
    .deselectAllButton.visible {
        height: 1.3em;
        opacity: 1;
        text-align: center;
        display: block;
        margin: 0 auto;
        margin-top: 0.5em;
    }
    
    .tagsLabel {
        display: block;
        margin: 0 auto;
        text-align: center;
    }
}

/* Mobile To Top Button */
.mobileToTopButton {
    position: fixed;
    bottom: 1.2rem;
    right: 1.2rem;
    width: 2.8rem;
    height: 4rem;
    border-radius: 1.25rem;
    background-color: var(--c-blue);
    color: white;
    border: none;
    font-size: 2.5rem;
    cursor: pointer;
    z-index: 1000;
    display: none;
    align-items: center;
    justify-content: center;
    box-shadow: 0 2px 16px rgba(0, 0, 0, 0.4);
    transition: all 300ms ease-in-out;
    font-weight:  bold;
    vertical-align: top; /* Aligns to top of container */
    line-height: 0.5;
    padding: 2px 10px 13px 10px;
}

.mobileToTopButton:hover {
    transform: scale(1.1);
    box-shadow: 0 6px 16px rgba(0, 0, 0, 0.4);
}

@media (max-width: 800px) {
    .mobileToTopButton {
        display: flex;
    }
}